import Mock from 'mockjs'
import { responseData } from './utils'

const Random = Mock.Random

Mock.mock('/getSettlementList', 'post', options => {
  const list = new Array(10).fill(1).map((a, idx) => {
    return {
      stmtName: Random.cword(2, 5),
      stmtType: '定期',
      stmtSts: Random.integer(0, 2),
      stmtMan: '担当1',
      stmtDate: new Date(),
      createTime: new Date()
    }
  })

  return responseData(10000000, {
    list: list,
    pageNum: Random.integer(1, 5),
    pageSize: 10,
    total: 90
  }, '')
})

