Mock.mock('/getLastDetail', 'post', options => {
  const params = typeof options.body === 'string' ? JSON.parse(options.body).body : options.body.body

  const list = new Array(params.pageSize).fill(1).map((a, idx) => {
    return {
      id: idx + 1,
      credits: Random.integer(-9000, 9000),
      model: Random.pick(['自动', '人工']),
      type: Random.pick(['发行', '核销']),
      user: Random.cname(),
      aid: 1,
      plat: Random.pick(['活动1', '活动2', '积分商城']),
      behavior: Random.pick(['注册认证', '新车主专享']),
      commodity: Random.cword(1, 5),
      account: Random.cword(1, 5),
      date: Random.datetime()
    }
  })

  return responseData(10000000, {
    list: list,
    pageNum: params.pageNum,
    pageSize: params.pageSize,
    total: 90
  }, '')
})
