const servicesName = {
  ...require('./serviceLogin'),
  ...require('./serviceLogout'),
  ...require('./serviceGetDeptConsole'),
  ...require('./serviceGetPlatConsole'),
  ...require('./serviceChartsData'),
  ...require('./serviceGetAcctActList'),
  ...require('./serviceGetDeptList'),
  ...require('./serviceGetStmtList'),
  ...require('./serviceGetAcctList'),
  ...require('./serviceCheckAcctDup'),
  ...require('./serviceSearchEmployee'),
  ...require('./serviceCheckEnterDup'),
  ...require('./serviceCreateAcct'),
  ...require('./serviceGetAcctApplDetail'),
  ...require('./serviceUpdateAcctApplSts'),
  ...require('./serviceVerifyAppl'),
  ...require('./serviceGetAcctDetail'),
  ...require('./serviceGetPointsDetail'),
  ...require('./serviceGetLimitChangeRecord'),
  ...require('./serviceGetApplDetail'),
  ...require('./serviceGetApplEditDetail'),
  ...require('./serviceGetUseImpRecord'),
  ...require('./serviceCreateAppl'),
  ...require('./serviceGetActList'),
  ...require('./serviceCreateAct'),
  ...require('./serviceGetActApplDetail'),
  ...require('./serviceGetActDetail'),
  ...require('./serviceGetStmtDetail'),
  ...require('./serviceGetLastDetail'),
  ...require('./serviceGetRulesList'),
  ...require('./serviceGetRulesDetail'),
  ...require('./serviceUpdateActSts')
}

export const installApi = $http => {
  Object.keys(servicesName).forEach(service => {
    $http[service] = (data = {}, options) => servicesName[service]($http, data, options)
  })
}
