import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import { installApi } from './install'
import { consoler } from '../utils'

const Axios = axios.create({
  baseURL: '',
  timeout: 60000, // 请求超时时间
  withCredentials: true
})

// 配置请求拦截器
Axios.interceptors.request.use(config => {
  const accountInfo = sessionStorage.getItem('accountInfo')
  if (accountInfo) {
    config.headers.access_token = accountInfo.token
  }

  config.data = {
    header: {
      appId: '',
      signature: '',
      timestamp: '',
      transactionId: ''
    },
    body: config.data
  }

  return config
}, error => {
  if (error.message && error.message.indexOf('timeout') >= 0) {
    Vue.prototype.$message.warning('请求超时')
    consoler('error', 'A200', error)
  } else {
    Vue.prototype.$message.error('请求异常')
    consoler('error', 'A100', error)
  }

  return Promise.reject(error)
})

// 配置响应拦截器
Axios.interceptors.response.use(res => {
  if (res.data && res.data.header) {
    // todo: 拦截登录跳转
    // if (res.data.header.code === '1007' || res.data.header.code === '1009') {
    //   Vue.prototype.$router.push('/login')
    // }

    if (res.data.header.code !== 10000000) {
      consoler('error', `B${res.data.header.code}`, res.data.header.message)
      return Promise.reject(res.data.header)
    }

    return res.data
  } else {
    consoler('error', 'B000', 'no header')
    return Promise.reject(res.data.header)
  }
}, error => {
  Vue.prototype.$message.error('响应异常')
  consoler('error', `H${error.response.status}`, error)

  error.code = error.response.status
  return Promise.reject(error)
})

Vue.use(VueAxios, Axios)

// 把接口api绑定在axios实例上，以便可以使用this.$http.xxx()的方式调用
installApi(Vue.prototype.$http)
