import Vue from 'vue'
import Moment from 'moment'

const colorGreen = '#19be6b'
const colorOrange = '#ff9900'
const colorRed = '#ed4014'
const colorBlack = '#909399'

// 账户状态文案
export const fmtCrdtAcctStatus = val => {
  switch (val) {
    case 0: return '正常'
    case 1: return '已关闭'
    case 2: return '申请中'
    case 3: return '未通过'
    case 4: return '已取消'
    case 5: return '已编辑'
    default: return '未知'
  }
}

// 活动状态文案
export const fmtActStatus = val => {
  switch (val) {
    case 0: return '已开始'
    case 1: return '已重启'
    case 2: return '已暂停'
    case 3: return '已结束'
    case 4: return '申请中'
    case 5: return '未通过'
    case 6: return '已取消'
    case 7: return '已编辑'
    case 8: return '已通过'
    default: return '未知'
  }
}

// 账户状态颜色
export const fmtCrdtAcctStatusColor = (val, type = 'color') => {
  let color = ''
  const styleObj = {}

  switch (val) {
    case 0: color = colorGreen; break
    case 1: color = colorRed; break
    case 2:
    case 3:
    case 4:
    case 5: color = colorOrange; break
    default: color = colorBlack
  }

  styleObj[type] = color
  return styleObj
}

// 账户申请状态颜色
export const fmtAcctApplStatusColor = (val, type = 'color') => {
  let color = ''
  const styleObj = {}

  switch (val) {
    case 0: color = colorGreen; break
    case 1:
    case 3: color = colorRed; break
    case 4: color = colorBlack; break
    case 2:
    case 5: color = colorOrange; break
    default: color = colorBlack
  }

  styleObj[type] = color
  return styleObj
}

// 活动状态颜色
export const fmtActStatusColor = (val, type = 'color') => {
  let color = ''
  const styleObj = {}

  switch (val) {
    case 0:
    case 1:
    case 8: color = colorGreen; break
    case 2:
    case 5: color = colorRed; break
    case 4:
    case 7: color = colorOrange; break
    default: color = colorBlack
  }

  styleObj[type] = color
  return styleObj
}

export const fmtMoney = amount => {
  try {
    if (!Number(amount)) return '-'

    const decimalCount = 0
    const decimal = '.'
    const thousands = ','
    const negativeSign = amount < 0 ? '-' : ''
    const i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString()
    const j = (i.length > 3) ? i.length % 3 : 0

    return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, '$1' + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : '')
  } catch (e) {
    return amount
  }
}

export const fmtFloat2Percent = float => {
  if (!float || typeof float !== 'number') return '-'

  return Number((Math.round(float * 10000) / 100).toFixed(2)) + '%'
}

export const fmtDateTime = date => {
  return Moment(date).format('YYYY-MM-DD HH:mm')
}

export const fmtDate = date => {
  return Moment(date).format('YYYY-MM-DD')
}

export const fmtCDate = date => {
  return Moment(date).format('YYYY年M月D日')
}

export const fmtStmtSts = val => {
  const sts = {
    0: '已结算',
    1: '部分已结算',
    2: '结算中'
  }
  return sts[val]
}

export const fmtStmtStsColor = (val, type = 'color') => {
  const styleObj = {}
  const sts = {
    0: colorGreen,
    1: colorOrange,
    2: colorOrange
  }

  styleObj[type] = sts[val]
  return styleObj
}

export const fmtBillStatus = val => {
  const sts = {
    0: '已确认',
    1: '待确认'
  }

  return sts[val]
}

export const fmtBillStatusColor = val => {
  const sts = {
    0: colorGreen,
    1: colorOrange
  }

  return { color: sts[val] }
}

const filters = {
  fmtCrdtAcctStatus,
  fmtActStatus,
  fmtCrdtAcctStatusColor,
  fmtAcctApplStatusColor,
  fmtActStatusColor,
  fmtMoney,
  fmtFloat2Percent,
  fmtDateTime,
  fmtDate,
  fmtCDate,
  fmtStmtSts,
  fmtStmtStsColor,
  fmtBillStatus,
  fmtBillStatusColor
}

Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
