import Vue from 'vue'
import VueRouter from 'vue-router'
import { toUpperFirstLetter } from '../utils'

const Login = () => import('../views/Login.vue')
const Frame = () => import('../views/Frame.vue')
const NotFound = () => import('../views/NotFound.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Frame',
    component: Frame,
    children: [],
    meta: { name: '' }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: { name: '登录' }
  }
]

const router = new VueRouter({
  routes
})

// 自定义方法，用来清除重复添加路由配置的警告
router.$addRoutes = params => {
  router.matcher = new VueRouter().matcher
  router.addRoutes(params)
}

// 从sessionStorage中设置账户信息到vuex
function storage2store (accountInfo) {
  if (router.app.$options.store.state.accountInfo) return

  router.app.$options.store.commit('setAccountInfo', accountInfo)
}

// 转换菜单列表为路由构建格式
function menu2route (menuList) {
  return menuList.map(item => {
    const componentName = toUpperFirstLetter(item.name)
    const routeConfig = {
      path: `${item.name}`,
      name: componentName,
      component: () => import('@/views' + (item.path || '') + '/' + componentName + '.vue'),
      meta: { name: item.label }
    }

    if (item.children) routeConfig.children = menu2route(item.children)
    if (item.alias) routeConfig.alias = item.alias

    return routeConfig
  })
}

// 动态生成路由
function createDynamicRoute (menuList) {
  const routes = router.options.routes
  routes[0].children = menu2route(menuList)
  // 把预设的routes列表的通配符改为addRoute加入，这样就不会出现刷新页面会匹配到通配符的路由组件中去了
  routes.push({
    path: '*',
    name: '404',
    component: NotFound,
    meta: { name: '404' }
  })
  router.$addRoutes(routes)
}

// 全局路由守卫，进入路由前校验账户信息
router.beforeEach((to, from, next) => {
  let accountInfo = sessionStorage.getItem('accountInfo')
  accountInfo = accountInfo && accountInfo !== 'undefined' ? JSON.parse(accountInfo) : null

  if (to.path !== '/login' && !accountInfo) {
    next('/login') // 没有登录则跳转到登录页
  } else {
    storage2store(accountInfo)
    // 如果主框架路由没有子菜单，则动态添加菜单路由
    if (accountInfo && accountInfo.menuList && (!router.options.routes[0].children || router.options.routes[0].children.length <= 0)) {
      createDynamicRoute(accountInfo.menuList)
      next({ ...to, replace: true })
    }

    next()
  }
})

export default router
