import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    accountInfo: null,
    deptList: null
  },
  getters: {
    getAccountInfo (state) {
      return state.accountInfo || {}
    },
    getDeptList (state) {
      return state.deptList || []
    }
  },
  mutations: {
    setAccountInfo (state, info) {
      state.accountInfo = info ? JSON.parse(JSON.stringify(info)) : null
    },
    setDeptList (state, list) {
      state.deptList = list ? JSON.parse(JSON.stringify(list)) : null
    }
  },
  actions: {
  },
  modules: {
  }
})
