<template>
  <i :class="['arrow-icon', arrowColor]">
    <slot></slot><i :class="arrow"></i>
  </i>
</template>

<script>
export default {
  name: 'arrow-icon',
  props: {
    value: [String, Number]
  },
  computed: {
    arrow () {
      const val = Number(this.value)
      return isNaN(val) ? '' : `el-icon${val > 0 ? '-top' : (val < 0 ? '-bottom' : '')}-right`
    },
    arrowColor () {
      const val = Number(this.value)
      return isNaN(val) ? '' : (val > 0 ? 'arrow-grow' : (val < 0 ? 'arrow-reduce' : ''))
    }
  }
}
</script>

<style lang="scss" scoped>
.arrow-icon {
  display: inline-flex;
  align-items: center;
  font-style: normal;

  &.arrow-grow { color: #19be6b; }
  &.arrow-reduce { color: #ed4014; }
}
</style>
