import Mock from 'mockjs'
import { responseData } from './utils.js'

Mock.setup({
  timeout: '400-800'
})

const Random = Mock.Random

require('./login.js')
require('./platform.js')
require('./account.js')
require('./activity')
require('./rules.js')
require('./settlement.js')

Mock.mock('/getAcctActList', 'post', options => {
  const actList = new Array(Random.natural(2, 5)).fill(1).map((a, idx) => {
    return {
      id: idx + 1,
      name: '活动-' + Random.ctitle()
    }
  })

  return responseData(10000000, actList, '')
})

Mock.mock('/getDeptList', 'post', options => {
  const actList = ['CF部', 'MA部', 'MK部'].map((a, idx) => {
    return {
      id: idx + 1,
      name: a
    }
  })

  return responseData(10000000, actList, '')
})

Mock.mock('/attachment', 'post', options => {
  const file = options.body.get('file')
  const ptIdx = file.name.lastIndexOf('.')
  const format = file.name.slice(ptIdx + 1).toUpperCase()
  const name = file.name.slice(0, ptIdx)

  return responseData(10000000, {
    fid: Random.natural(1, 1000000),
    name,
    format,
    size: file.size,
    createTime: new Date(),
    creater: '员工1',
    url: ''
  }, '')
})

Mock.mock('/verifyAppl', 'post', options => {
  return responseData(10000000, { isChanged: Random.boolean() }, '')
})
