import { PROXY } from './config'

const url = PROXY + '/verifyAppl'

export const serviceVerifyAppl = ($http, data, options) => {
  return new Promise((resolve, reject) => {
    $http
      .post(url, data, options)
      .then(res => {
        resolve(res.body)
      })
      .catch(reject)
  })
}
