export const consoler = function (type, errorCode, error) {
  console[type](`状态码-${errorCode} : `, error)
}

export const toUpperFirstLetter = function (word) {
  return word.slice(0, 1).toUpperCase() + word.slice(1)
}

export const toLowerFirstLetter = function (word) {
  return word.slice(0, 1).toLowerCase() + word.slice(1)
}
