import Mock from 'mockjs'
import { responseData } from './utils'

const Random = Mock.Random

Mock.mock('/getCrdtAcctList', 'post', options => {
  const params = typeof options.body === 'string' ? JSON.parse(options.body).body : options.body.body

  const list = new Array(params.pageSize).fill(1).map((a, idx) => {
    return {
      id: idx + 1,
      account: Random.cname(),
      deptName: Random.cname(),
      status: Random.integer(0, 5),
      budget: Random.natural(1000, 1000000),
      budgetAmount: Random.natural(100000, 10000000),
      budgetAmountLine: Random.float(0, 0, 0, 2),
      imprest: Random.natural(1000, 1000000),
      imprestAmount: Random.natural(100000, 10000000),
      imprestAmountLine: Random.float(0, 0, 0, 2),
      usedAmount: Random.natural(100000, 10000000),
      availableAmount: Random.natural(100000, 10000000)
    }
  })

  return responseData(10000000, {
    list: list,
    pageNum: params.pageNum,
    pageSize: params.pageSize,
    total: 90
  }, '')
})

Mock.mock('/checkAcctDup', 'post', options => {
  options.body = options.body ? JSON.parse(options.body) : {}
  const params = options.body.body || {}

  if (params.name === 'mt') {
    return responseData(10000001, '', '该积分账户名称已存在')
  } else {
    return responseData(10000000, {}, '')
  }
})

Mock.mock('/searchEmployee', 'post', options => {
  options.body = options.body ? JSON.parse(options.body) : {}
  const params = options.body.body || {}

  if (params.id === '0') {
    return responseData(10000000, {
      name: '员工1',
      mobile: '13000000000',
      email: 'abc@123.com'
    }, '')
  } else if (params.id === '1') {
    return responseData(10000001, '', '该员工号不存在')
  } else if (params.id === '2') {
    return responseData(10000001, '', '该员工号已注册')
  } else {
    return responseData(10000001, '', '该员工号不属于该部门')
  }
})

