Mock.mock('/getCrdtAcctDetail', 'post', options => {
  const info = {
    status: 0,
    account: '',
    type: 'GTMC业务部门',
    ownership: 'GTMC',
    deptId: 1,
    deptName: 'CF部',
    contact: 'CF部担当1',
    mobile: '13000000000',
    email: 'abc@123.com',
    attachments: []
  }
  const amountData = {
    totalAmount: 90000000,
    usedAmount: 40000000,
    availableAmount: 20000000,
    budget: 6000000,
    budgetAmount: 60000000,
    budgetAmountLine: 0.7,
    imprest: 2000000,
    imprestAmount: 30000000,
    imprestAmountLine: 0.65,
    issued: 20000000
  }
  const todayData = [
    {
      name: '本日积分发行量',
      value: Random.natural(10000, 1000000),
      lastVal: Random.natural(10000, 1000000),
      diff: 0.189
    },
    {
      name: '本日积分核销量',
      value: Random.natural(10000, 1000000),
      lastVal: Random.natural(10000, 1000000),
      diff: 0.189
    },
    {
      name: '本月积分发行量',
      value: Random.natural(10000, 1000000),
      lastVal: Random.natural(10000, 1000000),
      diff: 0.189
    },
    {
      name: '本月积分核销量',
      value: Random.natural(10000, 1000000),
      lastVal: Random.natural(10000, 1000000),
      diff: 0.189
    }
  ]
  const toDoList = [
    {
      id: 1,
      text: `<${Random.ctitle()}>预算额度已达警戒线。`,
      isDanger: true,
      type: 'acctBudgetAmountLine'
    },
    {
      id: 1,
      text: `<${Random.ctitle()}>备用额度已达警戒线。`,
      isDanger: true,
      type: 'acctImprestAmountLine'
    },
    {
      id: 1,
      text: `<${Random.ctitle()}>预算额度已达警戒线。`,
      isDanger: true,
      type: 'actBudgetAmountLine'
    },
    {
      id: 1,
      text: `<${Random.ctitle()}>备用额度已达警戒线。`,
      isDanger: true,
      type: 'acctImprestAmountLine'
    },
    {
      id: 1,
      text: '新的积分账户申请',
      isDanger: false,
      type: 'acctAppl'
    }
  ]
  const pointsOverview = ['已发行', '待发行', '未发行'].map((a, idx) => {
    return {
      name: a,
      integral: Random.natural(100000, 1000000),
      proportion: Random.float(0, 0, 0, 99)
    }
  })
  const unissuedOverview = ['已返还', '已作废', '已冻结'].map((a, idx) => {
    return {
      name: a,
      integral: Random.natural(100000, 1000000),
      proportion: Random.float(0, 0, 0, 99)
    }
  })
  let tmp = []
  let underWayAct = new Array(Random.natural(3, 20)).fill(1).map((a, idx) => {
    if (idx !== 0 && idx % 5 === 0) {
      const copy = tmp.map(t => Object.assign({}, t))
      tmp = [
        {
          id: idx,
          name: '活动' + idx,
          data: {
            budget: Random.natural(100000, 1000000),
            budgetAmount: Random.natural(100000, 1000000),
            budgetAmountLine: Random.float(0, 0, 50, 99),
            imprest: Random.natural(10000, 100000),
            imprestAmount: Random.natural(10000, 100000),
            imprestAmountLine: Random.float(0, 0, 50, 99),
            issued: Random.natural(10000, 100000)
          }
        }
      ]
      return copy
    } else {
      tmp.push({
        id: idx,
        name: '活动' + (idx + 1),
        data: {
          budget: Random.natural(100000, 1000000),
          budgetAmount: Random.natural(500000, 1000000),
          budgetAmountLine: Random.float(0, 0, 1, 2),
          imprest: Random.natural(100000, 1000000),
          imprestAmount: Random.natural(100000, 500000),
          imprestAmountLine: Random.float(0, 0, 1, 2),
          usedAmount: Random.natural(200000, 400000),
          issued: Random.natural(100000, 200000)
        }
      })
      return null
    }
  })
  underWayAct.push(tmp)
  underWayAct = underWayAct.filter(u => u)

  return responseData(10000000, {
    info,
    amountData,
    todayData,
    toDoList,
    pointsOverview,
    unissuedOverview,
    underWayAct
  }, '')
})

