import Mock from 'mockjs'
import { responseData } from './utils'

const Random = Mock.Random

Mock.mock('/getRulesList', 'post', options => {
  const params = typeof options.body === 'string' ? JSON.parse(options.body).body : options.body.body

  const list = new Array(params.pageSize).fill(1).map((a, idx) => {
    return {
      id: idx + 1,
      ruleId: Random.integer(100, 10000),
      ruleName: Random.cword(2, 5),
      ruleApp: Random.pick(['活动整体', '用户行为']),
      num: Random.integer(1, 100),
      desc: Random.csentence()
    }
  })

  return responseData(10000000, {
    list: list,
    pageNum: params.pageNum,
    pageSize: params.pageSize,
    total: 90
  }, '')
})

Mock.mock('/getRulesDetail', 'post', options => {
  const params = typeof options.body === 'string' ? JSON.parse(options.body).body : options.body.body

  const info = {
    ruleId: Random.integer(100, 10000),
    ruleName: Random.cword(2, 5),
    num: Random.integer(1, 100),
    desc: Random.csentence()
  }

  const list = new Array(params.pageSize).fill(1).map((a, idx) => {
    return {
      behavior: Random.pick(['抽奖', '签到', '答题']),
      status: Random.integer(0, 5),
      activity: Random.cword(2, 5),
      dept: Random.pick(['CF部', 'MK部', 'MA部']),
      actType: Random.pick(['常规', '节点']),
      actSts: Random.integer(0, 8),
      startTime: new Date(),
      endTime: new Date()
    }
  })

  return responseData(10000000, {
    info,
    list: list,
    pageNum: params.pageNum,
    pageSize: params.pageSize,
    total: 90
  }, '')
})
